@import '../../mixins/mixin.scss';

.slogan_banner{
  z-index: 1;
  color: #ffffff;
  position: relative;
  top: 20vh;
  overflow-x: hidden;
  -webkit-overflow-x: hidden !important;

  .span_slogan{
    text-align: center;
    font-size: 1.8em;
    display: flex;
    flex-direction: column;
    justify-content: flex-start !important;
    align-items: flex-start;
    padding-left: 1%;
  }

     .title_main{
      display: block;
      font-size: 1.6em;
      font-weight: bold;
    }
    
}
  
  .buttons{
    position: relative;
    @include flex(flex-start,center);
    z-index: 1;
    padding-top: 24vh;
    gap: 5vw;
    margin-left: 8px;
  }

  @media (min-width: 1800px){
   .title_main{
      display: block;
      font-size: 2.2em !important;
      font-weight: bold;
    }
    
    .sub_title_slogan{
      font-size: 1.3em;
    }
  }


  @media only screen and (orientation: portrait){
    .slogan_banner{
      margin-top: 30vh;
      margin-left: 10%;
    }
    .span_slogan{
      font-size: 1em;  
    }

    .buttons{
      margin-left: 32%;
      padding-top: 21vh;
    }
  }
  
  @media (max-width: 870px){

    .title_main{
      font-size: 1.3em !important;
    }

    .sub_title_slogan{
      font-size: 0.7em !important;
    }
  }


  @media (max-width: 780px){
    .title_main{
      font-size: 0.9em !important;
    }

    .sub_title_slogan{
      font-size: 0.5em !important;
    }
  }


@media (max-width: 500px){

  .slogan_banner{
    top: -20vh !important;
    left: -6%;
  }

  .buttons{
    gap: 14vw;
    top: -35vh;
    margin-left: 15%;
  }

  .title_main{
    font-size: 1em !important;
  }

  .sub_title_slogan{
    font-size: 0.6em !important;
  }

  .span_slogan{
    justify-content: center !important;
    align-items: center !important;
  }
  
}


